"use client"

import { useState } from "react"
import { useRouter } from "next/navigation"
import Link from "next/link"
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"
import { authAPI } from "@/lib/api"
import { useToast } from "@/hooks/use-toast"

export default function LoginPage() {
  const router = useRouter()
  const { toast } = useToast()
  const [email, setEmail] = useState("")
  const [password, setPassword] = useState("")
  const [isLoading, setIsLoading] = useState(false)

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setIsLoading(true)

    try {
      const response = await authAPI.login({ email, password })
      toast({
        title: "Success",
        description: "Logged in successfully!",
      })

      // Redirect based on user role
      if (response.user.is_admin) {
        router.push("/admin/dashboard")
      } else {
        router.push("/")
      }
    } catch (error: any) {
      toast({
        title: "Error",
        description: error.message || "Failed to login. Please check your credentials.",
        variant: "destructive",
      })
    } finally {
      setIsLoading(false)
    }
  }

  return (
    <div className="min-h-screen flex flex-col lg:flex-row">
      {/* Left - Form (Full width on mobile, half on desktop) */}
      <div className="flex-1 flex items-center justify-center p-6 sm:p-8 order-2 lg:order-1">
        <div className="w-full max-w-md">
          <Link href="/" className="inline-block mb-8 sm:mb-12">
            <h1 className="text-2xl font-bold tracking-[0.2em] text-[#2E5E99]">LUXE</h1>
          </Link>

          <h2 className="text-2xl sm:text-3xl font-bold mb-2 text-[#0D2440]">Welcome back</h2>
          <p className="text-[#2E5E99] mb-6 sm:mb-8" style={{ fontFamily: "var(--font-body)" }}>
            Enter your credentials to access your account
          </p>

          <form onSubmit={handleSubmit} className="space-y-5 sm:space-y-6">
            <div>
              <label
                className="text-sm font-medium mb-2 block text-[#0D2440]"
                style={{ fontFamily: "var(--font-body)" }}
              >
                Email
              </label>
              <Input
                type="email"
                placeholder="john@example.com"
                className="h-11 sm:h-12 border-[#7BA4D0] focus:border-[#2E5E99]"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                required
              />
            </div>
            <div>
              <label
                className="text-sm font-medium mb-2 block text-[#0D2440]"
                style={{ fontFamily: "var(--font-body)" }}
              >
                Password
              </label>
              <Input
                type="password"
                placeholder="••••••••"
                className="h-11 sm:h-12 border-[#7BA4D0] focus:border-[#2E5E99]"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                required
              />
            </div>
            <div className="flex flex-col sm:flex-row sm:items-center justify-between gap-3">
              <label
                className="flex items-center gap-2 text-sm text-[#0D2440]"
                style={{ fontFamily: "var(--font-body)" }}
              >
                <input type="checkbox" className="rounded border-[#7BA4D0]" />
                Remember me
              </label>
              <Link
                href="/forgot-password"
                className="text-sm text-[#2E5E99] hover:text-[#0D2440]"
                style={{ fontFamily: "var(--font-body)" }}
              >
                Forgot password?
              </Link>
            </div>
            <Button
              type="submit"
              size="lg"
              className="w-full uppercase tracking-wider bg-[#2E5E99] hover:bg-[#0D2440] text-[#E7F0FA] h-11 sm:h-12"
              disabled={isLoading}
            >
              {isLoading ? "Signing in..." : "Sign In"}
            </Button>
          </form>

          <p className="text-center mt-6 sm:mt-8 text-[#2E5E99]" style={{ fontFamily: "var(--font-body)" }}>
            Don't have an account?{" "}
            <Link href="/signup" className="text-[#0D2440] font-medium hover:underline">
              Sign up
            </Link>
          </p>
        </div>
      </div>

      {/* Right - Image (Smaller on mobile, shown at top) */}
      <div
        className="h-[30vh] lg:h-auto lg:flex-1 bg-cover bg-center order-1 lg:order-2"
        style={{ backgroundImage: "url('/elegant-fashion-model-in-blue-tones-sapphire-aesth.jpg')" }}
      >
        <div className="w-full h-full bg-gradient-to-b lg:bg-gradient-to-r from-[#0D2440]/60 to-transparent" />
      </div>
    </div>
  )
}
